@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    @variants responsive {
      /* Hide scrollbar for Chrome, Safari and Opera */
      .no-scrollbar::-webkit-scrollbar {
          display: none;
      }

      /* Hide scrollbar for IE, Edge and Firefox */
      .no-scrollbar {
          -ms-overflow-style: none;  /* IE and Edge */
          scrollbar-width: none;  /* Firefox */
      }
    }
  }


.literal {
  @apply rounded-tr-lg rounded-bl-lg;
}

.identifier {
  @apply rounded-tr-lg rounded-bl-lg;
}

.draggable {
  @apply cursor-grab hover:bg-slate-700;
}

.empty-leaf {
  @apply bg-slate-500;
}

.expression {
  @apply rounded-tr-lg;
}
